import React, { useState, useEffect } from 'react';
import { EditOutlined, EllipsisOutlined, SettingOutlined } from '@ant-design/icons';
import { Avatar, Card, Skeleton, Tabs, Col, Row } from 'antd';

const { Meta } = Card;

export const ProductComponent = () => {
    const [loading, setLoading] = useState(true);
    const [currentKey, setCurrentKey] = useState(0);

    const dl = [1, 2, 3, 4, 5, 6, 7, 8, 9];

    const RenderSelectedGroupItems = () => {
        return (
            <Row wrap gutter={16} className="product-row" justify="space-between">
                {dl.map(i => (
                    <Col xs={24} sm={12} md={8} lg={5} className="product-col">
                        <Card
                            key={i}
                            style={{
                                width: '100%',
                                marginTop: 16,
                            }}
                            hoverable
                            actions={[
                                <SettingOutlined key="setting" />,
                                <EditOutlined key="edit" />,
                                <EllipsisOutlined key="ellipsis" />,
                            ]}
                            cover={<img alt="example" src={require('../assets/logo.png')} style={{ height: 200 }} />}
                        >
                            <Skeleton loading={loading} avatar active>
                                <Meta
                                    avatar={<Avatar src={require('../assets/logo.png')} />}
                                    title="Product title"
                                    description="Product description"
                                />
                            </Skeleton>
                        </Card>
                    </Col>
                ))}
            </Row>
        );
    }

    useEffect(() => {
        setLoading(true);
        setTimeout(() => {
            setLoading(false);
        }, 2000);
    }, [currentKey]);

    return (
        <div className="product-container">
            <Tabs
                tabPosition="left"
                onChange={active => setCurrentKey(active)}
                items={[
                    {
                        label: 'Kategori 1',
                        key: 1,
                        children: <RenderSelectedGroupItems />,
                        animated: true

                    },
                    {
                        label: 'Kategori 2',
                        key: 2,
                        children: <RenderSelectedGroupItems />
                    },
                    {
                        label: 'Kategori 3',
                        key: 3,
                        children: <RenderSelectedGroupItems />
                    },
                ]}
            />
        </div>
    );
}